.calc-subtitle {
  padding-top: 20px;
  margin-bottom: 20px;
  font-family: 'Inconsolata', monospace;
  font-size: 18px;
  font-weight: 600;
}

.calc-label__data {
  position: absolute;
  height: 1px;
  top: 0px;
  left: 10px;
  padding: 0 5px;
  width: 110px;
  background-color: #ffffff;
  color: #f4ba25;
}

.calc-label__data span {
  color: #255ff4;
  padding-left: 5px;
}

.calc-label__indicators {
  position: absolute;
  height: 1px;
  top: 0px;
  left: 10px;
  padding: 0 5px;
  width: 120px;
  background-color: #ffffff;
  color: #f4ba25;
}

.calc-label-name {
  position: absolute;
  left: 20px;
  top: -6px;
  color: #f76d43;
  font-family: 'Inconsolata', monospace;
  font-size: 16px;
  font-weight: 400;
}

.calc-label-name span {
  color: #255ff4;
  padding-left: 5px;
}

.noUi-target {
  position: absolute;
  width: 90%;
  bottom: 10px;
  border: none;
}

.noUi-connect {
  height: 0%;
  width: 0%;
  background-color: inherit;
}

.noUi-touch-area {
  height: 100%;
  width: 100%;
  background-color: #f76d43;
  border-radius: 100%;
  cursor: pointer;
}

.noUi-horizontal {
  height: 0px;
}

.noUi-horizontal .noUi-handle {
  width: 15px;
  height: 15px;
  border-radius: 100%;
  top: 2px;
}

.noUi-handle::after,
.noUi-handle::before {
  display: none;
}

.noUi-horizontal .noUi-tooltip {
  display: none;
}

.calc {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px 0;
}

.wrapper {
  display: block;
  max-width: 500px;
  width: 100%;
  margin: 0 auto;
  padding: 30px 15px;
  background-color: #f0f8ff;
  border-radius: 50px;
}

