/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type=number] {
  -moz-appearance: textfield;
}

/* modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1050;
  display: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal__dialog {
  max-width: 500px;
  margin: 40px auto;
}

.modal__container {
  position: relative;
  width: 100%;
  padding: 40px;
  background-color: #ffe4c4;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 4px;
  max-height: 80vh;
  overflow-y: auto;
}

.modal__content {
  display: block;
  text-align: center;
  max-width: 200px;
  margin: 0 auto;
}

h3 {
  text-align: center;
  font-size: 22px;
  text-transform: uppercase;
  font-family: 'Roboto', sans-serif;
  font-weight: 900px;
  font-size: 22px;
  line-height: 28px;
  color: #000;
}

.modal__close {
  position: absolute;
  top: 8px;
  right: 14px;
  font-size: 30px;
  color: #000;
  opacity: .5;
  font-weight: 700;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.modal__title {
  text-align: center;
  font-size: 22px;
  text-transform: uppercase;
}

.modal__input {
  display: block;
  margin: 20px auto 20px auto;
  width: 280px;
  height: 50px;
  background: #fff;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
  border: none;
  font-size: 18px;
  text-align: center;
  padding: 0 20px;
  outline: 0;
}

#modal-btn {
  display: block;
  width: 200px;
  margin: 0 auto;
  border: none;
  padding: 10px;
  background: #0B5FFF;
  border-radius: 3px;
  font-family: 'Roboto', sans-serif;
  font-weight: 400px;
  font-size: 16px;
  line-height: 22px;
  color: #fff;
  text-align: center;
}

.show {
  display: block;
}

.hide {
  display: none;
}

/*browser arrows-input  */
.list-group__li {
  display: block;
  list-style: none;
  font-family: 'Roboto', sans-serif;
  font-weight: 400px;
  font-size: 16px;
  line-height: 22px;
  color: #000;
  text-align: center;
}
